import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';

import {
  LineChart,
  Line,
  YAxis,
	XAxis,
  CartesianGrid,
  Tooltip,
  Legend,
	ResponsiveContainer
} from "recharts";

import Switch from '@material-ui/core/Switch';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import Slider from '@material-ui/core/Slider';
import { Divider, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
	sliderHolder: {
		width: 150,
		paddingLeft: 10,
		paddingRight: 10
	},
	smallCaption: {
		fontSize: 14,
	},
	boldCaption: {
		fontSize: 14,
		fontWeight: 'bold'
	},
	divider: {
		marginTop: 10,
		marginBottom: 10
	},
	switchHolder: {
		paddingRight: 10
	},
	switchLabel: {
		fontSize: 13,
	},
	slider: {
		marginLeft: 10
	}

}));

const RelativeRt = (props) => {
	const { mainFileData, selectedMethod } = props;

	const [chartData, setChartData] = React.useState({results: [], contexts: []}); // default: {results: [], contexts: []}
	const [toInc, setToInc] = React.useState(0); // default: 0
	const [toShow, setToShowMax] = React.useState(100); // default: 100
	const theme = useTheme();

	

	useEffect(() => {
		if (!mainFileData.isValidId(selectedMethod.id)) return;
		const parseChartData = () => {
			const results = [];

			const current_id = selectedMethod.id;
	
			const contexts = mainFileData.getContexts(current_id);

			const contextRuntimesRef = {};

			contexts.forEach((con) => {
				contextRuntimesRef[con] = mainFileData.getContextRuntimes(current_id, con);
			});
	
			
			let i = (toInc*1000);
			while (true) {
				const item = {};

				for (let ci = 0; ci < contexts.length; ci++) {
					const con = contexts[ci];
					const vals = contextRuntimesRef[con][i];
					if (vals !== undefined) {
						item[con] = vals;
					}
				}
				i++;
				if ( i > (toInc*1000 + toShow) || Object.keys(item).length === 0) break; // break when no new entries can be constructed
				results.push(item);
			}
	
			return {results, contexts};
		}
	
		setChartData(parseChartData());
	}, [mainFileData,selectedMethod.id, theme.palette.type, toInc, toShow])
	

	const [gridVisible, setGridVisible] = React.useState(false); // default: false
	const [dotVisible, setDotVisible] = React.useState(false); // default: false
	const [boldSelected, setBoldSelected] = React.useState(true); // default: true

	const [toSkip, setToSkip] = React.useState(15); // default: 15

	const classes = useStyles();
	
	return (
		<div style={{
			display: 'flex',
			justifyContent: 'center',
			alignItems: 'center',
			flex: 1,
		}}>
			<ResponsiveContainer width={"80%"} minHeight={50} maxHeight={"90%"}>
				<LineChart
					data={chartData.results.slice(toSkip)}
					margin={{
						top: 5,
						right: 30,
						left: 20,
						bottom: 5
					}}
				>
					{ gridVisible && <CartesianGrid strokeDasharray="3 3" />}
					<YAxis yAxisId="left" />
					<XAxis />
					<Legend />
					<Tooltip />
					{
						chartData.contexts.map((e, index) => 
							<Line
								key={`relativert-line-${index}`}
								name={e.length < 20 ? e : `...${e.slice(-17)}` }
								strokeWidth={(selectedMethod.context === e) && boldSelected ? 3 : undefined}
								yAxisId="left"
								type="monotone"
								dataKey={e === "" ? index : e}
								dot={dotVisible}
								stroke={ mainFileData.getContextColor(e) }
								activeDot={{ r: (selectedMethod.context === e) && boldSelected ? 5 : undefined }}
								animationDuration={600}
							/>
						)
					}
				</LineChart>
			</ResponsiveContainer>
			<FormGroup >
				

				

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Skip first <b>{toSkip}</b></Typography>}
					labelPlacement="top"
					control={
						<Slider
							className={classes.slider}
							color="secondary"
							valueLabelDisplay="auto"
							onChange={(c, val) => setToSkip(val)}
							value={toSkip}
							step={5}
							marks
							min={0}
							max={50}
						/>
					}
				/>

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Data <b>{`[${toInc*1000} - ${toInc*1000 + 1000}]`}</b></Typography>}
					labelPlacement="top"
					control={
						<Slider
							className={classes.slider}
							color="secondary"
							valueLabelDisplay="auto"
							onChange={(c, val) => setToInc(val)}
							value={toInc}
							step={1}
							marks
							min={0}
							max={100}
						/>
					}
				/>

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Showing <b>{toShow}</b> datapoints</Typography>}
					labelPlacement="top"
					control={
						<Slider
							className={classes.slider}
							color="secondary"
							valueLabelDisplay="auto"
							onChange={(c, val) => setToShowMax(val)}
							value={toShow}
							step={50}
							marks
							min={50}
							max={1000}
						/>
					}
				/>

				<Divider className={classes.divider}/>

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Grid</Typography>}
					labelPlacement="start"
					control={
					<Switch
						size="small"
						checked={gridVisible}
						onChange={() => setGridVisible(!gridVisible)}
					/>
					}
				/>

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Dots</Typography>}
					labelPlacement="start"
					control={
					<Switch
						size="small"
						checked={dotVisible}
						onChange={() => setDotVisible(!dotVisible)}
					/>
					}
				/>

				<FormControlLabel
					label={<Typography className={classes.smallCaption}>Bold</Typography>}
					labelPlacement="start"
					control={
					<Switch
						size="small"
						checked={boldSelected}
						onChange={() => setBoldSelected(!boldSelected)}
					/>
					}
				/>

			</FormGroup>
		</div>
  );
}

export default RelativeRt;