const CompilationLog = ({mainFileData, id}) => {
	const classes = useStyles();
	return mainFileData.isValidId(id) &&
		mainFileData.getMethodsThatCompiled(id).map((method, index) => 
			<div key={`${method[0]}-list-${method[2]}-${index}`}>
				<Chip style={{ backgroundColor: mainFileData.getContextColor(method[2]) }} label={`C-${mainFileData.getContextIndex(method[2])}`} className={classes.chip}/>
				<Typography align="right" className={classes.assumptions} variant="subtitle2">
					{getConT(method[2]).assumptions.join(" ")}
				</Typography> 
				<Typography align="right" className={classes.typeFeedback} variant="subtitle1">
					{getConT(method[2]).typeAssumptions.join(" ")}
				</Typography>
				<Typography align="right" className={classes.typeFeedback} variant="subtitle1">
					{getConT(method[2]).missing} miss
				</Typography>
				<Typography className={classes.caption} variant="subtitle1">
					Basic Blocks: {method[7]}
				</Typography>
				<Typography className={classes.caption} variant="subtitle1">
					Promises: {method[8]}
				</Typography>
				<Typography gutterBottom className={classes.caption} variant="subtitle1">
					Cmp + Opt: {method[5] + method[6]}
				</Typography>
				<Typography gutterBottom className={classes.caption} variant="subtitle1">
					Total: {method[3]}
				</Typography>
				<Typography gutterBottom className={classes.caption} variant="subtitle1">
					Total Runs: {mainFileData.getContextRuns(method[0],method[2])}
				</Typography>
				<Divider/>
			</div>
	)
}

const Compilations = (props) => {
	const classes = useStyles();
	const theme = useTheme();
	const { mainFileData, selectedMethod, height } = props;
	
	const [compilationVRuntime, setCompilationVRuntime] = React.useState(0); // default: 0
	

	const [avgRt, setAvgRt] = React.useState([]); // default: []


	const [baselineRuns, setBaselineRuns] = React.useState(0); // default: 0
	const [lastN, setLastN] = React.useState(15); // default: 15
	const [circleCoords, setCircleCoords] = React.useState([]); // default: []

	const radius = 18;

	const [latticeEdges, setLatticeEdges] = React.useState([]); // default: []

	const latticeRef = useRef(null);


	const reset = () => {
		setCompilationVRuntime(0);
		setAvgRt([]);
		setBaselineRuns(0);
	}

	const CustomTooltip = ({ active, payload, label }) => {
		if (active && payload && payload.length) {
			return (
				<div className={classes.tooltipHolder}>
					<Typography className={classes.smallCaption}>
						<b>{payload[0].payload.name} [{payload[0].payload.lastN} runs]: </b> {payload[0].value}
					</Typography>
				</div>
			);
		}
	
		return null;
	};

	const updateStaticData = async () => {
		const rt = mainFileData.getRuntime(selectedMethod.id);
		const res = ((rt.rir2pir + rt.opt) / rt.runtime) * 100;
		setCompilationVRuntime(res);
		setBaselineRuns(mainFileData.getContextRuns(selectedMethod.id, "baseline"));
	}

	const updateBarChart = async (n) => {
		const res = [];
		mainFileData.getContexts(selectedMethod.id).forEach((context, index) => {
			const {value, lastN} = mainFileData.getLastNAvg(selectedMethod.id,context,n);
			if (value !== -1) {
				res.push({
					name: "C-" + mainFileData.getContextIndex(context), // context index
					value,
					lastN,
					context
				})
			}
		});
		res.sort((a,b) => a.value < b.value ? 1 : -1);
		setAvgRt(res);
		setLastN(n);
	}


	useEffect(() => {
		if (!mainFileData.isValidId(selectedMethod.id)) return reset();
		updateStaticData();
		updateBarChart(lastN);
		plotLattice();

	}, [selectedMethod.id, mainFileData]);

	const plotLattice = () => {
		if (!mainFileData.isValidId(selectedMethod.id)) return reset();

		// Lattice Plotter Data
		if (latticeRef.current === null || latticeRef.current.clientHeight === null) return;
		

		const contexts = mainFileData.getContexts(selectedMethod.id);


		const ciData = {};

		
		const checker = (arr, target) => target.length === 0 ? true : target.every(v => arr.includes(v));
		const contains = (o, c) => {
			const other = getConT(o);
			const curr = getConT(c);
			
			return (
				other.missing === curr.missing &&
				checker(other.assumptions, curr.assumptions) &&
				checker(other.typeAssumptions, curr.typeAssumptions)
			);
		}

		contexts.forEach((curr) => {
			let res = [];
			contexts.forEach((other) => {
				if (curr !== other && contains(other,curr)) res.push(other)
			});
			ciData[curr] = res;
		});

		ciData["baseline"] = contexts;

		const index = ciData["baseline"].indexOf("baseline");
		ciData["baseline"].splice(index, 1); // remove processed leaves from map
		

		const getLevelData = (ciData) => {
			const getLeaves = (d) => {
				const leaves = [];
				for (var key in d) {
					if (d.hasOwnProperty(key) && d[key].length === 0) {
						leaves.push(key);
					}
				}
				return leaves;
			}
			const map = {...ciData};
			const levelData = [];
			while(Object.keys(map).length !== 0) {
				const leaves = getLeaves(map);
				levelData.push(leaves);			
				leaves.forEach(e => delete map[e]); // remove processed leaves
	
				for (var key in map) {
					if (map.hasOwnProperty(key)) { 
						
						let j = 0;
						for (j=0;j<leaves.length;j++) {
							const e = leaves[j];
							const newArr = [...map[key]];
							const index = newArr.indexOf(e);
							if (index !== -1) {
								newArr.splice(index, 1); // remove processed leaves from map
							}
							map[key] = newArr;
	
						}
					}
				}
			}
			return levelData;
		}

		// ciData says, I am contained in all these contexts
		// const ciData = containedInData; // contained in data
		const levelData = getLevelData(ciData);
		const contextCoordMap = {};
		const levels = levelData.length;
		const generateCoordinates = () => {
			if (levels === 0 || latticeRef.current === null) return [];
			const displayHeight = latticeRef.current.clientHeight;
			const displayWidth = latticeRef.current.clientWidth;
			const increment_per_level = parseInt(displayHeight / levels) + 1;
			const coords = [];
			let i = 0;
			let l = 0;
			for (i=0;i<displayHeight;i+=increment_per_level) {
				let centerY = i + increment_per_level/2;
				let levelNodes = levelData[l];
				let node_seperation = parseInt(displayWidth / (levelNodes.length)) + 1;
				let j = 0;
				let m = 0;
				for (j=0;j<displayWidth;j+=node_seperation) {
					let x = (j + node_seperation/2);
					let y = centerY;
					let currentContext = levelNodes[m];
					contextCoordMap[currentContext] = {x, y}
					coords.push([x,y,currentContext]);
					m++;
				}
				l++;
			}
			return coords;
		}

		const generateEdges = () => {

			const getTarget = (node,depth) => {
				const level = levelData[depth];
				if (level === undefined) return ["baseline"];
				let i = 0;
				let res = [];
				for(i=0;i<level.length;i++) {
					const e = level[i];
					if (ciData[e].indexOf(node) !== -1) {
						res.push(e);
					}
				}
				if (res.length > 0) return res
				else return getTarget(node, depth + 1);
			}

			const edges = [];
			let i = 0;
			let current = levelData[i];
			
			while (current !== undefined) {
				let j = 0;
				for (j=0;j<current.length;j++) {
					const node = current[j];
					const targets = getTarget(node, i + 1);
					targets.forEach(target => 
						edges.push([contextCoordMap[node].x,contextCoordMap[node].y,contextCoordMap[target].x,contextCoordMap[target].y])
					);
				}
				i++;
				current = levelData[i];
			}

			return edges;
		}

		const updateLatticeDiagram = () => {
			const c = generateCoordinates();
			const edges = generateEdges();

			setCircleCoords(c);
			setLatticeEdges(edges);
		}


		window.addEventListener("resize", updateLatticeDiagram, false);

		updateLatticeDiagram();

	}

	const showTooltip = (evt, text) => {
		let tooltip = document.getElementById("tooltip-lattice");
		const con = getConT(text);
		tooltip.innerHTML = con.assumptions.join(" ") + "<br/>" + con.typeAssumptions.join(" ") + "<br>" + "missing: " + con.missing;
		tooltip.style.display = "block";
		tooltip.style.left = evt.pageX + 10 + 'px';
		tooltip.style.top = evt.pageY + 10 + 'px';
	}
	
	const hideTooltip = () => {
		var tooltip = document.getElementById("tooltip-lattice");
		tooltip.style.display = "none";
	}

	const invertColor = (input) => {
		const hex = input.slice(1);
		console.log(hex);
		return "#" +  (Number(`0x1${hex}`) ^ 0xFFFFFF).toString(16).substr(1).toUpperCase()
	}

	return (
		<div className={classes.root}>
			<div className={classes.leftContainer}>
				<ResponsiveContainer width="100%" height={height / 2}>
					<BarChart width={15} data={avgRt}>
						<YAxis />
						<XAxis dataKey="name" />
						<Bar dataKey="value">
							{
								avgRt.map((entry, index) => (
									<Cell
										key={`${selectedMethod.id}-bar-art-${entry.name.split("-")[1]}`}
										strokeWidth={entry.context === selectedMethod.context ? 2 : 0}
										stroke={'#000'}
										fill={mainFileData.getContextColor(entry.context)}
										/>
									))
							}
						</Bar>
						<Tooltip content={CustomTooltip} />
					</BarChart>
				</ResponsiveContainer>
				{
					mainFileData.isValidId(selectedMethod.id) &&
					<div className={classes.sliderHolder}>
						<Slider
							className={classes.slider}
							color="secondary"
							valueLabelDisplay="auto"
							onChange={(c, val) => updateBarChart(val)}
							value={lastN}
							step={5}
							marks
							min={5}
							max={50}
						/>
					</div>
				}

				{mainFileData.isValidId(selectedMethod.id) &&<Typography className={classes.smallCaption} align="center">
					Relative Average Runtimes (Last {lastN}% runs)
				</Typography>}

				{mainFileData.isValidId(selectedMethod.id) &&<Typography className={classes.smallCaption} align="left">
					Considers last N% runs for each version for calculating the average runtime. Hovering over the
					graph shows the number of runs considered to calculate the average for that version.
				</Typography>}
				
			</div>
			<div className={classes.rightContainer}>
				<div className={classes.rlContainer}>
					<svg ref={latticeRef} height="100%" width="100%" >
						{
							latticeEdges.map((e,i) => <line key={`${selectedMethod.id}-edge-${i}`} x1={e[0]} y1={e[1]} x2={e[2]} y2={e[3]} stroke={theme.palette.secondary.main} strokeWidth={1.5} />)
						}
						{
							circleCoords.map((e,i) => {
							// <g id="UrTavla">
								// <circle
								// 	onMouseMove={(event) => showTooltip(event, e[2])}
								// 	onMouseOut={hideTooltip} key={`${selectedMethod.id}-node-${i}`}
								// 	cx={e[0]}
								// 	cy={e[1]}
								// 	r={radius}
								// 	stroke={e[2] === selectedMethod.context ? "black" : "gray"}
								// 	strokeWidth={e[2] === selectedMethod.context ? 2 : 1}
								// 	fill={mainFileData.getContextColor(e[2])}
								// >
							// 	</circle>
							// 	<text x="0" y="0" fill="red">Hello</text>
							// </g>
							return <g key={`${selectedMethod.id}-node-${i}`} id="UrTavla">
								<circle
									onMouseMove={(event) => showTooltip(event, e[2])}
									onMouseOut={hideTooltip} 
									cx={e[0]}
									cy={e[1]}
									r={radius}
									stroke={e[2] === selectedMethod.context ? "black" : "gray"}
									strokeWidth={e[2] === selectedMethod.context ? 2 : 1}
									fill={mainFileData.getContextColor(e[2])}
								>
									</circle>
								<text x={e[0]} y={e[1]} text-anchor="middle" fill="white" style={{fontSize: 15, fontFamily:"mono", fontWeight: "bold", mixBlendMode: "difference"}} dy={radius/2 - 3} >{mainFileData.getContextRuns(selectedMethod.id,e[2])}</text>
							</g>
							}
							)
						}
						Sorry, your browser does not support inline SVG.  
					</svg>
					<Typography className={classes.boldCaption} align="center" gutterBottom>
						{mainFileData.isValidId(selectedMethod.id) && `${compilationVRuntime.toString().substring(0,4)} % of runtime spent on compilation`}
					</Typography>
				</div>

				<div className={classes.rrContainer}>
					<div className={classes.compilationOrder}>
						{ mainFileData.isValidId(selectedMethod.id) &&
							<CompilationLog mainFileData={mainFileData} id={selectedMethod.id} />
						}
					</div>
					<Typography className={classes.boldCaption} align="right">
						Baseline Runs: {baselineRuns.toString()}
					</Typography>
				</div>

			</div>
			<div id="tooltip-lattice" display="none" className={classes.tooltipLattice} ></div>
		</div>
  );
  }
  
	export default Compilations;