import { useState, useEffect } from 'react';
import { createTheme } from '@material-ui/core/styles';
import { pink, blueGrey } from '@material-ui/core/colors';

const lightTheme = createTheme({
  palette: {
    type: 'light',
    primary: {
      main: blueGrey[600],
    },
    secondary: {
      main: pink[500]
    },
  },
});

const darkTheme = createTheme({
  palette: {
    type: 'dark',
    primary: {
      main: blueGrey[800],
    },
    secondary: {
      main: pink[600]
    },

  },
});

// theme toggler hook

export function useThemeToggle() {
  const [themeDark, setThemeDark] = useState(localStorage.getItem("theme") !== null ? localStorage.getItem("theme") : "dark" ); // default: dark
  function onchange() {
    if(themeDark === "dark") {
      localStorage.setItem('theme', 'light');
      setThemeDark("light");
    } else {
      localStorage.setItem('theme', 'dark');
      setThemeDark("dark");
    }
  }
  
  const darkMode = themeDark === "dark"
  const backgroundStyle = { backgroundColor: darkMode ? "#303030" : "#fafafa"};
  return { backgroundStyle, currentTheme: darkMode ? darkTheme : lightTheme,  onchange}
}

// height calculator hook

export function useHeight(optionalRef, maxHeightNormally, other, mainFileData) {
  const [height, setHeight] = useState(0);

  useEffect(() => {
    const handleResize = () => {
      let height = maxHeightNormally() - other;
      if(optionalRef.current !== null ) {
        height = maxHeightNormally() - (optionalRef.current.clientHeight + other);
      };
      setHeight(height);
    }
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [optionalRef, mainFileData]);

  return height;
}