import RshMethod from '../classes/RshMethod';

export default function jsonFileParser(s, selectedJSONMetadata) {
  const mainFileData = new RshMethod(
    s.s_runtime,
    s.s_calls,
    s.s_contexts,
    s.methodCTMap,
    s.methodCSMap,
    s.totalRuntime,
    s.methodMeta,
    s.methodsThatCompiledMap,
    s.methodRuntimeMap,
    s.methodContextMap,
    s.methodCallMap,
    selectedJSONMetadata,
    s.boxContextCallRuns,
    s.boxContextResolution
  );
  return mainFileData;
};