import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';

import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from 'recharts';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
	root: {
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		flex: 1,
	},
	leftContainer: {
		flex: 1,
	},
	rightContainer: {
		flex: 3,
		padding: theme.spacing(1)
	},
	table: {
    minWidth: 650,
  },

}));


const Info = (props) => {
	const { mainFileData, selectedMethod } = props;
	const classes = useStyles();
	const theme = useTheme();

	const [chartData, setChartData] = React.useState([]); // default: {results: [], contexts: []}

	const [currentData, setCurrentData] = React.useState([0,0,0,0]); // default: []


	useEffect(() => {
		if (!mainFileData.isValidId(selectedMethod.id)) return;
		const contextRuntime = mainFileData.getContextTotalRuntime(selectedMethod.id, selectedMethod.context);
		const totalRuntime = mainFileData.getRuntime(selectedMethod.id).runtime;

		const currentPercent = (contextRuntime/totalRuntime) * 100;

		const runtimes = mainFileData.getContextRuntimes(selectedMethod.id, selectedMethod.context);
		const data = [];
		if (runtimes.length > 0) {
			const sum = runtimes.reduce((a, b) => a + b, 0);
			const avg = (sum / runtimes.length) || 0;
			
			const rtS = [...mainFileData.getContextRuntimes(selectedMethod.id, selectedMethod.context)];
			rtS.sort((a, b) => a - b);
			const max = rtS[rtS.length - 1];
			const min = rtS[0];
			data.push(min);
			data.push(max);
			data.push(avg);

			const lastN = Math.round(runtimes.length * 0.05);
			if (lastN === 0) {
				data.push("Not Enough Data");
			} else {
				const s = runtimes.slice(-lastN);
				const sum = s.reduce((a, b) => a + b, 0);
				const avg = (sum / s.length) || 0;
				data.push(avg);
			}

		}

		setCurrentData(data);
	
		setChartData([{ name: 'Others', value: parseInt(100 - currentPercent) }, { name: 'This Context', value: parseInt(currentPercent) }]);
	}, [selectedMethod, mainFileData])
	
	return (
		<div className={classes.root} style={{
			display: 'flex',
			justifyContent: 'center',
			alignItems: 'center',
			flex: 1,
		}}>
			<div className={classes.leftContainer}>
				<ResponsiveContainer width="100%" height={200}>
					<PieChart width={300} height={200}>
						<Tooltip />
						<Pie data={chartData} dataKey="value" cx="50%" cy="50%" innerRadius={40} outerRadius={50} fill="#82ca9d" label >
							<Cell fill="gray" />
							<Cell fill={theme.palette.secondary.main} />
						</Pie>
					</PieChart>
				</ResponsiveContainer>
				<Typography variant="subtitle2" align="center">
					{mainFileData.isValidId(selectedMethod.id) && "Version Runtime V Total Runtime"}
				</Typography>
			</div>
			<div className={classes.rightContainer}>
				{
					mainFileData.isValidId(selectedMethod.id) && <div>
						<Typography variant="body1" style={{ fontWeight: 'bold' }}>
							{mainFileData.getName(selectedMethod.id)}
						</Typography>
						<Typography variant="subtitle2">
							{selectedMethod.context}
						</Typography>
					</div>
				}
				{
					currentData.length !== 0 && 
					<TableContainer component={Paper}>
						<Table className={classes.table} size="small">
							<TableHead >
								<TableRow>
									<TableCell>Min</TableCell>
									<TableCell>Max</TableCell>
									<TableCell>Avg</TableCell>
									<TableCell>Avg (Last 5%)</TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								<TableRow hover> 
									<TableCell >{currentData[0]}</TableCell>
									<TableCell >{currentData[1]}</TableCell>
									<TableCell >{currentData[2]}</TableCell>
									<TableCell >{currentData[3]}</TableCell>
								</TableRow>
							</TableBody>
						</Table>
					</TableContainer>
				}

				{
					mainFileData.isValidId(selectedMethod.id) && <div style={{ marginTop: 10 }}>

					<Typography variant="subtitle1" gutterBottom>
						<b>Version Runtime:</b> {mainFileData.getContextTotalRuntime(selectedMethod.id, selectedMethod.context)} 
						<b> ms | RIR2PIR:</b> {mainFileData.getContextCompileTime(selectedMethod.id, selectedMethod.context)}
						<b> ms | OPT:</b>     {mainFileData.getContextOptTime(selectedMethod.id, selectedMethod.context)} 
						<b> ms | Runs:</b>     {mainFileData.getContextRuns(selectedMethod.id, selectedMethod.context)} 
					</Typography>

					<Typography variant="subtitle1" gutterBottom>
						Version Spent {
							(((mainFileData.getContextCompileTime(selectedMethod.id, selectedMethod.context) + mainFileData.getContextOptTime(selectedMethod.id, selectedMethod.context))
							/ mainFileData.getContextTotalRuntime(selectedMethod.id, selectedMethod.context)) * 100).toString().substr(0,4)
						} % of its total runtime in compilation
					</Typography>


					</div>
				}
			</div>
		</div>
  );
}
export default Info;