import { useState, useEffect } from 'react';

export function useFileReader() {
  const [selectedJSON, setSelectedJSON] = useState(null); // default: null
  const [selectedJSONMetadata, setSelectedJSONMetadata] = useState({}); // default: {}

  function updateFile(file){
    setSelectedJSON(file)
  }

  function updateMeta(meta) {
    setSelectedJSONMetadata(meta);
  }

  function reset() {
    setSelectedJSON(null);
    setSelectedJSONMetadata({});
  }

  return { selectedJSON, meta: selectedJSONMetadata, updateFile, updateMeta, reset }
}