import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

import RelativeRT from './UA/RelativeRT';
import Compilations from './UA/Compilations';
import CallBox from './UA/CallBox';

import CallGraph from './Static/CallGraph';
import Info from './Static/Info';

const PADDING_TOP = 16;
const PADDING_BOTTOM = 16;

const TOP_PERCENT = 0.6;

const ACTION_BAR_HEIGHT = 0;

const useStyles = makeStyles((theme) => ({
	paper: {
		color: theme.palette.text.secondary,
		height: "100%",
		scrollbarWidth: "none",
		display: 'flex'
	},
	gridContainer: {
		height: "100%"
	},
	gridItemContainer: {
		padding: theme.spacing(2),
		paddingTop: PADDING_TOP,
		paddingBottom: PADDING_BOTTOM,
		overflow: "hidden"
	},
	formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
	topTab: {
		position: 'absolute',
		right: 20
	}
}));

const views = [
	{
		name: "Relative Runtimes",
		component: RelativeRT
	},
	{
		name: "Compilations",
		component: Compilations
	},
	{
		name: "Call Box",
		component: CallBox
	}
];

const viewsStatic = [
	{
		name: "Info",
		component: Info
	},
	{
		name: "Method Calls",
		component: CallGraph
	},
]

const UAComponents = (props) => {
	const classes = useStyles();
	const { selectedMethod, mainFileData, height, updateSelected } = props;

	const [topTab, setTopTab] = React.useState(0); // default: 0
	const [bottomTab, setBottomTab] = React.useState(0); // default: 0

	const handleChangeTop = (event, i) => {
		setTopTab(event.target.value);
  };

	const handleChangeBottom = (event, i) => {
		setBottomTab(event.target.value);
  };

	const topCardHeight = (height * TOP_PERCENT  - (PADDING_BOTTOM/2 + PADDING_TOP/2));
	const bottomCardHeight = (height * (1 - TOP_PERCENT)  - (PADDING_BOTTOM/2 + PADDING_TOP/2));

	return (
		<Grid container spacing={0} className={classes.gridContainer}>
			<Grid item xs={12}>
				<div className={classes.gridItemContainer} style={{ height: topCardHeight }}> 
					<Paper className={classes.paper}>
						<div className={classes.topTab}>
							<FormControl className={classes.formControl}>
								<Select
									value={topTab}
									onChange={handleChangeTop}
								>
									{
										views.map((e, i) => <MenuItem key={`top-views-sel-${i}`} value={i}>{e.name}</MenuItem>)
									}
								</Select>
							</FormControl>
						</div>
						{
							views.map((e, i) => {
								let C = e.component; 
								return topTab === i && <C height={topCardHeight} key={`top-views-${i}`} updateSelected={updateSelected} mainFileData={mainFileData} selectedMethod={selectedMethod}/> 
							})
						}
					</Paper>
				</div>
			</Grid>
			<Grid item xs={12}>
				<div className={classes.gridItemContainer} style={{height: bottomCardHeight}}> 
					<Paper className={classes.paper} >
						<div className={classes.topTab}>
							<FormControl className={classes.formControl}>
								<Select
									value={bottomTab}
									onChange={handleChangeBottom}
								>
									{
										viewsStatic.map((e, i) => <MenuItem key={`bottom-views-sel-${i}`} value={i}>{e.name}</MenuItem>)
									}
								</Select>
							</FormControl>
						</div>
						{
							viewsStatic.map((e, i) => {
								let C = e.component; 
								return bottomTab === i && <C height={bottomCardHeight} key={`bottom-views-${i}`} updateSelected={updateSelected} mainFileData={mainFileData} selectedMethod={selectedMethod}/> 
							})
						}
					</Paper>
				</div>
			</Grid>
		</Grid>
    );
}

export default UAComponents;