import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import MethodSelector from './MethodSelector';
import UAComponents from './UAComponents';


const PADDING_TOP = 16;
const PADDING_BOTTOM = 16;

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		backgroundColor: theme.palette.background.default
	},
	paper: {
		color: theme.palette.text.secondary,
		height: "100%",
		overflow: "scroll",
		scrollbarWidth: "none",
		display: 'flex'
	},
	gridContainer: {
		height: "100%"
	},
	gridItemContainer: {
		padding: theme.spacing(2),
		paddingTop: PADDING_TOP,
		paddingBottom: PADDING_BOTTOM,
		overflow: "hidden"
	}
}));




const LoggerView = (props) => {
	const classes = useStyles();
	const { height, mainFileData } = props;
	
	const [selectedMethod, setSelectedMethod] = React.useState({}); // default: {}
	

	const updateSelected = (id, context) => {
		setSelectedMethod({id, context});
	}

	return (
		<Grid container spacing={0} className={classes.gridContainer}>
			<Grid item xs={2}>
				<div className={classes.gridItemContainer} style={{height}}>
					<Paper className={classes.paper}>
						<MethodSelector mainFileData={mainFileData} updateSelected={updateSelected} />
					</Paper>
				</div>
			</Grid>
			<Grid item xs={10}>
				<UAComponents updateSelected={updateSelected} selectedMethod={selectedMethod} mainFileData={mainFileData} height={height} />
			</Grid>
		</Grid>
    );
}

export default LoggerView;