import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';

import { FormControlLabel, FormGroup, Slider, Switch, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
	root: {
		display: 'flex',
		// justifyContent: 'center',
		// alignItems: 'center',
		flex: 1,
    padding: theme.spacing(4)
	},
  leftSide: {
    flex: 4,
    // backgroundColor: 'red'
  },
  rightSide: {
    flex: 1,
    padding: theme.spacing(1)
  },

	smallCaption: {
		fontSize: 14,
	},
  callboxContainer: {
    flex: 6,
    display: "flex",
    flexDirection: "column"
  
  },
  svgContainer: {
    flex: 1,
  },
  x_run: {
    display: "flex",
    flexDirection: "row"
  },
  x_tag: {
    flex: 1,
    textAlign: "center",
    fontSize: 12,
    fontWeight: "bold",
    fontFamily: "mono"
  },
  yAxis: {
    display: "flex",
    flex: 1,
  },
  y_run: {
    display: "flex",
    flexDirection: "column",
  },
  y_tag: {
    flex: 1,
    textAlign: "center",
    fontSize: 12,
    fontWeight: "bold",
    fontFamily: "mono",
    display: "flex",
    justifyContent: "center",
  }

}));

const CallBoxCell = ({id,data, XMAX, YMAX, mainFileData}) => {
  const verticalGrowth   = 100 / YMAX;
  const horizontalGrowth = 100 / XMAX; 
  return [...Array(XMAX)].map((x, i) => 
    [...Array(YMAX)].map((x, j) => {
      // 0 * 100 + 99 = 0                 - (0                 + YMAX - 1)
      // 1 * 100 + 99 = YMAX              - (YMAX              + YMAX - 1)
      // 2 * 100 + 99 = (YMAX + YMAX - 1) - ((YMAX + YMAX - 1) + YMAX - 1)
      // ...
      const element = data[(i*YMAX) + j];
      const yCoord = ((100-verticalGrowth) - (verticalGrowth * j));
      const xCoord = horizontalGrowth * i;
      
      if (element !== undefined) {
        return <rect key={`cell-${id}-${i}-${j}`} x={`${xCoord}%`} y={`${yCoord}%`} width={`${horizontalGrowth}%`} height={`${verticalGrowth}%`} fill={mainFileData.getContextColor(element)} />
      } else {
        return <g key={`cell-${id}-${i}-${j}`} />;
      }
    })
  )
  

}


const CallBox = (props) => {
	const { mainFileData, selectedMethod } = props;
	const classes = useStyles();
	const theme = useTheme();

  const [boxChartData, setBoxChartData] = React.useState([]); // default: {}
  const [XMAX, setXMAX] = React.useState(10); // default: 10
  const [YMAX, setYMAX] = React.useState(50); // default: 50

  const [gridVisible, setGridVisible] = React.useState(true); // default: true

	useEffect(() => {
		if (!mainFileData.isValidId(selectedMethod.id)) return;
    const getContextCallRuns = mainFileData.getContextCallRuns(selectedMethod.id);
    setBoxChartData(getContextCallRuns);
  }, [selectedMethod.id, mainFileData])
	
	return (
		<div className={classes.root}>
      <div className={classes.callboxContainer}>
        <div className={classes.x_run}>
          { XMAX < 35 && 
            [...Array(XMAX)].map((_, i) => <div className={classes.x_tag}>Run <br/> {YMAX * (i + 1)}</div>) 
          }
        </div>
        <div className={classes.yAxis}>
          <div className={classes.svgContainer}>
            <svg className={classes.leftSide} height="100%" width="100%" >
              <CallBoxCell id={selectedMethod.id} data={boxChartData} mainFileData={mainFileData} XMAX={XMAX} YMAX={YMAX} />

              {gridVisible &&
                [...Array(XMAX)].map((_, i) => { 
                  const x = ((100 / XMAX) + ((100 / XMAX) * i)) + "%"; 
                  return <line key={`grid1-${selectedMethod.id}-${i}`} x1={x} y1={"0%"} x2={x} y2={"100%"} stroke={theme.palette.secondary.main} strokeWidth={1} />
                }) 
              }

              {gridVisible &&
                [...Array(YMAX)].map((_, i) => { 
                  const y = ((100 / YMAX) + ((100 / YMAX) * i)) + "%"; 
                  return <line key={`grid2-${selectedMethod.id}-${i}`} x1={"0%"} y1={y} x2={"100%"} y2={y} stroke={theme.palette.secondary.main} strokeWidth={1} />
                }) 
              }
              Sorry, your browser does not support inline SVG.  
            </svg>
          </div>
        </div>
        <div className={classes.x_run}>
          { XMAX < 35 && 
            [...Array(XMAX)].map((_, i) => <div className={classes.x_tag}>Run <br/> {(YMAX * i) + 1}</div>) 
          }
        </div>
      </div>
      <div className={classes.rightSide}>
      <FormGroup>
          <FormControlLabel
            label={<Typography className={classes.smallCaption}><b>{XMAX}</b> Stacks</Typography>}
            labelPlacement="top"
            control={
              <Slider
                defaultValue={10}
                color="secondary"
                value={XMAX}
                onChange={(c, val) => setXMAX(val)}
                step={5}
                marks
                min={5}
                max={100}
              />
            }
          />

          <FormControlLabel
            label={<Typography className={classes.smallCaption}><b>{YMAX}</b> Rows Per Stack</Typography>}
            labelPlacement="top"
            control={
              <Slider
                defaultValue={10}
                color="secondary"
                value={YMAX}
                onChange={(c, val) => setYMAX(val)}
                step={1}
                min={1}
                max={1000}
              />
            }
          />

          <FormControlLabel
            label={<Typography className={classes.smallCaption}>Grid</Typography>}
            labelPlacement="start"
            control={
            <Switch
              size="small"
              checked={gridVisible}
              onChange={() => setGridVisible(!gridVisible)}
            />
            }
          />
      </FormGroup>
      </div>
		</div>
  );
}
export default CallBox;