import React, { useEffect } from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
// import ReactJson from 'react-json-view'

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Divider, IconButton } from '@material-ui/core';

import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';

const useStyles = makeStyles((theme) => ({
	tabPanelContainer: {
		margin: theme.spacing(2),
	},
	root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    // paddingLeft: theme.spacing(4),
  },
	formControl: {
    width: '100%',
		marginTop: 20,
		marginBottom: 20
  },
}));
  
function TabPanel(props) {
	const { children, value, index, ...other } = props;
	const classes = useStyles();
	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{value === index && (
				<div className={classes.tabPanelContainer}>
					{children}
				</div>
			)}
		</div>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired,
};

const Item = (props) => 
	<div key={props.index}>
		<ListItem button onClick={() => props.handleOpened(props.index)}>
			<ListItemText primary={`${props.mainFileData.getName(props.methodId)} [${props.mainFileData.getVersionCount(props.methodId)}]` } secondary={`[${props.mainFileData.getRuntime(props.methodId).effect.toString().substring(0,4)}%] ${props.mainFileData.getRuntime(props.methodId).runtime.toString().substring(0,9)} ms, ${props.mainFileData.getRuns(props.methodId)} calls`} />
			{props.open.indexOf(props.index) !== -1 ? <ExpandLess/> : <ExpandMore/>}
		</ListItem>

		<Collapse in={props.open.indexOf(props.index) !== -1} timeout="auto" unmountOnExit>
			<List component="div" dense disablePadding>
				{
					props.mainFileData.getContexts(props.methodId).map((context, jindex) => 
						<ListItem key={`method-sub-${props.index}-${jindex}`} className={props.classes.nested}>
							<ListItemIcon>
								<Checkbox
									edge="start"
									checked={props.checked.indexOf(`${props.index}_${jindex}`) !== -1}
									tabIndex={-1}
									style={{
										color: props.mainFileData.getContextColor(context)
									}}
									onClick={() => props.handleCheckedChange(`${props.index}_${jindex}`)}
								/>
							</ListItemIcon>
							<div style={{overflow: "scroll"}}>{context}</div>
							<ListItemSecondaryAction>
								<IconButton disabled={props.selected === `${props.methodId}-method-sub-${props.index}-${jindex}`} onClick={() => { props.setSelected(`${props.methodId}-method-sub-${props.index}-${jindex}`); props.updateSelected(props.methodId, context)}} edge="end">
									<KeyboardArrowRightIcon />
								</IconButton>
							</ListItemSecondaryAction>
						</ListItem>
					)
				}
			</List>
		</Collapse>
		<Divider />
	</div>

const MethodSelector = (props) => {
	const { mainFileData, updateSelected } = props;

	const [tabValue, setTabValue] = React.useState(1); // default: 0
	const [tabSort, setTabSort] = React.useState(0); // default: 0

	const [checked, setChecked] = React.useState([]); // default: 0

	const [selected, setSelected] = React.useState(""); // default: ""

	const [sortData, setSortData] = React.useState([]); // default: ""

	const classes = useStyles();
	const [open, setOpen] = React.useState([]); // default: []

	const handleOpened = (value) => {
		const currentIndex = open.indexOf(value);
    const result = [...open];

    if (currentIndex === -1) {
      result.push(value);
    } else {
      result.splice(currentIndex, 1);
    }
    setOpen(result);
  };	
	
	const handleTabChange = (event, newValue) => {
		setTabValue(newValue);
	};

	const handleTabSortChange = (event) => {
		setTabSort(event.target.value);
		setOpen([])
  };

	const handleCheckedChange = (value) => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);

  };

	
	useEffect(() => {
		const sorts = [];
		sorts[0] = mainFileData.getSortedByRuntime();
		sorts[1] = mainFileData.getSortedByCalls();
		sorts[2] = mainFileData.getSortedByContexts();
		setSortData(sorts);
	}, [mainFileData])

	return (
		<div style={{ width: '100%' }}>
			<Tabs
				value={tabValue}
				indicatorColor="secondary"
				textColor="secondary"
				onChange={handleTabChange}
				scrollButtons="auto"
				variant="scrollable"
			>
				<Tab label="Files" />
				<Tab label="Methods" />
				<Tab label="Raw Data" />
			</Tabs>
			<TabPanel value={tabValue} index={0}>
				<Typography gutterBottom>
					{mainFileData.getMetadata().name}
				</Typography>
			</TabPanel>

			<TabPanel value={tabValue} index={1}>
				<FormControl className={classes.formControl}>
					<InputLabel id="sort-by">Sort By</InputLabel>
					<Select
						labelId="sort-by"
						value={tabSort}
						onChange={handleTabSortChange}
					>
						<MenuItem value={0}>Runtime</MenuItem>
						<MenuItem value={1}>Calls</MenuItem>
						<MenuItem value={2}>Contexts</MenuItem>
					</Select>
				</FormControl>

				{
					sortData.length === 3 && <List dense>
					{
						sortData[tabSort].map((methodId, index) => {
							return (
								<Item key={`${methodId}-${index}`} selected={selected} setSelected={setSelected} mainFileData={mainFileData} index={index} methodId={methodId} open={open} classes={classes} checked={checked} handleOpened={handleOpened} handleCheckedChange={handleCheckedChange} updateSelected={updateSelected} />
							);
						})
					}
				</List>
				}
				
			</TabPanel>
			<TabPanel value={tabValue} index={2}>
				<div style={{ backgroundColor: 'red' }}>
					{/* <ReactJson
						iconStyle="triangle"
						src={mainFileData.getRawMap()}
						collapsed={1}
						indentWidth={1}
						theme="eighties"
						name={false}
						quotesOnKeys={false}
						displayDataTypes={false}
						/> */}
				</div>
			</TabPanel>
			
		</div>
	);
}

export default MethodSelector;