export default class RshMethod {
  constructor(s_runtime, s_calls, s_contexts, methodCTMap, methodCSMap, total, methodMeta, methodsThatCompiledMap, methodRuntimeMap, methodContextMap, methodCallMap,selectedJSONMetadata, boxContextCallRuns,boxContextResolution) {
    this.methodCTMap = methodCTMap;
    this.methodCSMap = methodCSMap;
    this.totalRuntime = total;
    this.methodMeta = methodMeta;
    this.methodsThatCompiledMap = methodsThatCompiledMap;
    this.methodRuntimeMap = methodRuntimeMap;
    this.methodContextMap = methodContextMap;
    this.methodCallMap = methodCallMap;
    this.metaData = selectedJSONMetadata;

    this.s_runtime = s_runtime;
    this.s_calls = s_calls;
    this.s_contexts = s_contexts;

    this.boxContextCallRuns = boxContextCallRuns
    this.boxContextResolutionOriginal = boxContextResolution;

    // create an inverted index for lookup
    const ret = {};
    const colorMap = {};
    const getRandomColor = () => {
      var letters = '0123456789ABCDEF';
      var color = '#';
      for (var i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
      }
      return color;
    }
    for(var key in boxContextResolution){
      ret[boxContextResolution[key]] = key;
      const color = key === "baseline" ? "#a0a0a0" : getRandomColor();
      if (colorMap[boxContextResolution[key]] === undefined) colorMap[boxContextResolution[key]] = color;
      if (colorMap[key] === undefined) colorMap[key] = color;
    }
    this.boxContextResolution = ret;
    this.colorMap = colorMap;

  }

  getContextIndex(context) {
    return this.boxContextResolutionOriginal[context];
  }

  getContextColor(indexOrContext) {
    return this.colorMap[indexOrContext];
  }

  getContextCallRuns(id) {
    return this.boxContextCallRuns[id];
  }

  resolveContextByIndex(index) {
    return this.boxContextResolution[index];
  }

  getSortedByRuntime() {
    return [...this.s_runtime];
  }

  getSortedByCalls() {
    return [...this.s_calls];
  }

  getSortedByContexts() {
    return [...this.s_contexts];
  }

  getVersionCount(id) {
    return this.methodContextMap[id].length;
  }

  getName(id) {
    return this.methodMeta[id].name;
  }

  getRuns(id) {
    return this.methodMeta[id].runs;
  }

  getRuntime(id) {
    return this.methodRuntimeMap[id];
  }

  getContexts(id) {
    return [...this.methodContextMap[id]];
  }

  getCalledMethodIds(id) {
    if (this.methodCallMap[id] === undefined) return []
    return this.methodCallMap[id];
  }

  getMetadata() {
    return this.metaData;
  }

  getContextRuntimes(id, context) {
    return this.methodCSMap[id][context];
  }

  getContextTotalRuntime(id, context) {
    return this.methodCSMap[id][context].reduce((a, b) => a + b, 0);
  }

  getBoxData(id) {
    return [...this.methodsThatCompiledMap[id]];
  }

  getMethodsThatCompiled(id) {
    if (this.methodsThatCompiledMap[id] === undefined) return []
    return [...this.methodsThatCompiledMap[id]];
  }

  getContextRuns(id, context){
    if (this.methodCTMap[id][context] == undefined) return 0;
    return this.methodCTMap[id][context].runs;
  }

  getContextCompileTime(id, context){
    return this.methodCTMap[id][context].cmp;
  }

  getContextOptTime(id, context){
    return this.methodCTMap[id][context].opt;
  }

  getContextRunTime(id, context){
    return this.methodCTMap[id][context].run;
  }

  getContextAvgRuntimeNoComp(id, context){
    return (
      (this.methodCTMap[id][context].run - (this.methodCTMap[id][context].cmp + this.methodCTMap[id][context].opt))
      / this.methodCTMap[id][context].runs
    );
  }

  getLastNAvg(id, context, n) {
    const runtimes = this.methodCSMap[id][context];
    const lastN = Math.round(runtimes.length * (n / 100));
    if (lastN <= 0) {
      return { value: -1, lastN};
    } else {
      const s = runtimes.slice(-lastN);
      const sum = s.reduce((a, b) => a + b, 0);
      const avg = (sum / s.length) || 0;
      return { value: avg, lastN};
    }
  }

  isValidId(id) {
    return this.methodRuntimeMap[id] !== undefined;
  }
}