import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

import BugReportIcon from '@material-ui/icons/BugReport';

import CardActions from '@material-ui/core/CardActions';

import Files from "react-files";

import { useFileReader } from '../hooks/fileReader';


const PADDING_TOP = 16;
const PADDING_BOTTOM = 16;

const useStyles = makeStyles((theme) => ({
	paper: {
		color: theme.palette.text.secondary,
		height: "100%",
		overflow: "scroll",
		scrollbarWidth: "none",
	},
  paperText: {
		color: theme.palette.text.secondary,
		scrollbarWidth: "none",
    marginTop: theme.spacing(1),
    padding: theme.spacing(2)
  },
	mainDataContainer: {
		padding: theme.spacing(2),
		display: "flex",
		flexDirection: "column"
	},
	gridContainer: {
		height: "100%"
	},
	gridItemContainer: {
		padding: theme.spacing(2),
		paddingTop: PADDING_TOP,
		paddingBottom: PADDING_BOTTOM,
		overflow: "hidden"
	},
  gridItemContainerLeft: {
    padding: theme.spacing(2),
		paddingTop: PADDING_TOP,
		paddingBottom: PADDING_BOTTOM,
		overflow: "scroll"
  },
	heading: {
		marginBottom: theme.spacing(2),
	},
	formControl: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
		width: 400
  },
	button: {
		width: 200,
		alignSelf: 'left'
	},
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  secTitle: {
    fontSize: 12,
    fontWeight: 'bold'
  },
  pos: {
    marginBottom: 12,
  },
  jsonCard: {
    borderRadius: theme.shape.borderRadius,
    marginTop: 20,
    marginBottom: 10
  },
  reportIcon: {
    color: 'tomato',
    fontSize: 50
  }
}));

const Init = (props) => {
	const classes = useStyles();
	const { height, start } = props;

  const currentFile = useFileReader();

  
  
  const fileReader = new FileReader();
  
  fileReader.onload = event => {
    try {
      currentFile.updateFile(JSON.parse(event.target.result));
    } catch(e) {
      currentFile.reset();
    }
  }

  const bull = <span className={classes.bullet}>•</span>;

	return (
		<Grid container spacing={0} className={classes.gridContainer}>
			<Grid item xs={12}>
				<Grid container spacing={0} className={classes.gridContainer}>
					<Grid item xs={7}>
            <div className={classes.gridItemContainerLeft} style={{height}}>
              <Card >
                <CardContent>
                  <Typography variant="h5" component="h2" gutterBottom>
                    rsh{bull}viz
                  </Typography>
                  <Typography className={classes.pos} color="textSecondary">
                    Simple Runtime visualizer for Ř
                  </Typography>
                  <Typography className={classes.pos} color="textSecondary">
                    IIT Mandi CompL Group
                  </Typography>
                </CardContent>
              </Card>
              <Paper className={classes.paperText} >
              </Paper>
            </div>
					</Grid>
					<Grid item xs={5}>
						<div className={classes.gridItemContainer} style={{height}}> 
							<Paper className={classes.paper} >
								<div className={classes.mainDataContainer}>
									<Typography className={classes.heading} variant="h4" gutterBottom={true} align="center">
                    Session Settings
									</Typography>                  
                  {
                    currentFile.selectedJSON &&
                    <Card elevation={2} className={classes.jsonCard}>
                      <CardContent>
                        <BugReportIcon className={classes.reportIcon} style={{  }} />
                        <Typography className={classes.title} color="textSecondary" gutterBottom>
                          {currentFile.meta.name}
                        </Typography>
                        <Typography className={classes.secTitle} color="textSecondary" gutterBottom>
                          {(new Date(currentFile.meta.lastModified)).toLocaleDateString()}
                        </Typography>
                        <Typography variant="h5" component="h2">
                          {currentFile.meta.sizeReadable}
                        </Typography>
                        <CardActions>
                          <Button size="small" onClick={currentFile.reset}>Delete</Button>
                        </CardActions>
                      </CardContent>
                    </Card>
                  }
									<FormControl className={classes.formControl}>
                    <Button variant="outlined" component="span" className={classes.button}>
                      <Files
                        onChange={file => {
                          fileReader.readAsText(file[0]);
                          currentFile.updateMeta({ name: file[0].name, sizeReadable: file[0].sizeReadable, lastModified: file[0].lastModified })
                        }}
                        onError={err => console.log(err)}
                        accepts={[".json"]}
                        clickable
                      >
                        {
                          !currentFile.updateFile ? "Upload runtime JSON" : "select another file"
                        }
                        
                      </Files>
                    </Button>
									</FormControl>
									<Button onClick={() => start(currentFile.selectedJSON, currentFile.meta)} disabled={currentFile.selectedJSON === null} variant={'contained'} className={classes.button} color="secondary">
                    Start
									</Button>
								</div>
							</Paper>
						</div>
					</Grid>
				</Grid>
			</Grid>
		</Grid>
	);
}

export default Init;