import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';


const useStyles = makeStyles((theme) => ({
	root: {
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		flex: 1,
	},
	chip: {
		margin: 10
	}
}));

const CallGraph = (props) => {
	const classes = useStyles();
	const { selectedMethod, mainFileData, updateSelected } = props;
	
	return (
		<div className={classes.root}>
			{
				mainFileData.isValidId(selectedMethod.id) && mainFileData.getCalledMethodIds(selectedMethod.id).map((ele) => 
					<Chip
						key={`call-graph-${ele}`}
						className={classes.chip}
						label={mainFileData.getName(ele)}
						onClick={() => { updateSelected(ele, "baseline"); }}
					/>
				)
			}
				
		</div>
  );
}
  
export default CallGraph;