import React, { useState, useRef } from 'react';
import AppBar from './AppBar.js';
import { ThemeProvider } from '@material-ui/core/styles';
import Init from './components/Init';
import LoggerView from './components/LoggerView';

import jsonFileParser from './helpers/jsonFileParser';
import { useThemeToggle, useHeight } from './hooks/layout';

const PADDING_TOP = 16;
const PADDING_BOTTOM = 16;

const App = () => {
  const [mainFileData, setMainFileData] = useState(null); // default: null 
  const theme = useThemeToggle();
  const ref = useRef(null);
  const height = useHeight(ref, () => window.innerHeight, PADDING_BOTTOM + PADDING_TOP, mainFileData);

  function updateFileData(selectedJSON, selectedJSONMetadata) {
    setMainFileData(jsonFileParser(selectedJSON, selectedJSONMetadata))
  }

  return (
    <ThemeProvider theme={theme.currentTheme}>
      {
        !mainFileData && 
        <div ref={ref}>
          <AppBar style={{ margin: 0 }} toggleTheme={theme.onchange} />
        </div>
      }
      <div style={theme.backgroundStyle}>
        {
          mainFileData !== null ? 
            <LoggerView height={height} mainFileData={mainFileData}/> 
            : 
            <Init height={height} start={updateFileData}/>
        }
      </div>
    </ThemeProvider>
  );
}

export default App;